import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { ActivatedRoute, Route } from '@angular/router';

@Component({
  selector: 'bb-routable-tab-set',
  templateUrl: './routable-tab-component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RoutableTabComponent implements OnInit {
  public tabs: Array<{ title: string; route: string; dataRole: string }> | undefined;

  constructor(private readonly route: ActivatedRoute) {}

  ngOnInit() {
    const routeConfig = this.route.snapshot.routeConfig;
    if (routeConfig && routeConfig.children?.length) {
      this.tabs = this.createTabsFromRouteChildren(routeConfig.children);
    }
  }

  private createTabsFromRouteChildren(routeChildren: Route[]) {
    return routeChildren
      .filter((routeItem) => routeItem.path && routeItem.data?.title)
      .map((routeItem) => ({
        title: (routeItem.data as Record<string, string>).title,
        route: routeItem.path as string,
        dataRole: (routeItem.data as Record<string, string>).title.split(' ').join('-').toLowerCase(),
      }));
  }
}
